.nav-full{
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  height: 100vh;
  background-color: black;
}

.nav-center {
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
  height: auto;
}

.nav-full-line {
  margin: auto;
  margin-top: 120px;
  border-bottom: 1px solid rgba(255, 255 , 255, 0.6);
  height: 1px;
  width: 60%;
}

.nav-tran {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: none;
}



