.AlwaysOnT {
  position: fixed;
  width: 90%;
  height: auto;
  background: black;
  z-index: 999;

  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Spinner {
  position: fixed;
  width: 90%;
  height: auto;
  
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 999;
}

.Spinnerbg{
  background-color: rgb(25 , 31, 38);
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
}

.fullscreen {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: grey;
  z-index: 999;
  margin: auto;
}



